* {
    font-family: Arial, Helvetica, sans-serif;
}
body{
    padding: 60px;
}
input{
    font-size: 1.2rem;
    color: grey;
    width: 100%;
    padding: 5px 0px 5px 10px;
}

.card {
    align-content: center;
    border: 1px solid grey;
    border-radius: 6px;
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.card-grid{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.card p {
    text-transform: uppercase;
    padding: 5px 10px;
    text-align: center;
}
.card img {
    object-fit: contain;
    padding: 10px;
    max-height: 170px;
}
